<template>
  <div>
    <h1>Full Name : {{ fullname }}</h1>
  </div>
</template>
<script>
export default {
  data() {
    return {
      firstName: "inseo",
      lastName: "jeong",
    };
  },
  // computed와 watch는 둘다 vue인스턴스 내의 정의된 데이터 값에 변경이 일어나는지 감시하고, 변경 될 때 마다 정의된 함수가 실행됨
  // computed 와 watch의 차이점
  // computed 는 기존에 정의된 데이터값을 기반으로 새로운 데이터 값을 활용하기 위해 사용된다면,
  //watch는 watch에 정의된 데이터 값 하나만 감시하기 위한 용도로 사용된다.
  computed: {
    fullname() {
      return this.firstName + " " + this.lastName;
    },
  },
};
</script>
<style></style>
